import React, {Component} from 'react';
import Delete from '@material-ui/icons/Delete';
import {withStyles} from '@material-ui/core/styles';
import {
    Button,
    IconButton,
    List,
    ListItem,
    ListItemSecondaryAction,
    ListItemText,
    Paper,
    TextField,
    Typography,
    Container
} from '@material-ui/core';

const styles = theme => ({
    root: {margin: theme.spacing(0.5), padding: theme.spacing(0.5), maxWidth: 400},
    form: {display: 'flex', alignItems: 'baseline', justifyContent: 'space-evenly'}
});

export default withStyles(styles)(
    class App extends Component {
        state = {
            exercises: [{id: 1, title: 'Bench Press'}, {id: 2, title: 'Deadlift'}, {id: 3, title: 'Squats'}],
            title: ''
        };

        handleChange = ({target: {name, value}}) => this.setState({[name]: value});

        handleCreate = e => {
            e.preventDefault();
            if (this.state.title) {
                this.setState(({exercises, title}) => ({
                    exercises: [...exercises, {title, id: Date.now()}],
                    title: ''
                }));
            }
        };

        handleDelete = id => this.setState(({exercises}) => ({exercises: exercises.filter(ex => ex.id !== id)}));

        render() {
            const {title, exercises} = this.state;
            const {classes} = this.props;
            return <Container maxWidth="sm">
                <Paper className={classes.root}>
                    <Typography variant='h2' align='center' gutterBottom>Exercises</Typography>
                    <form className={classes.form} onSubmit={this.handleCreate}>
                        <TextField name='title' label='Exercise' value={title} onChange={this.handleChange}
                                   margin='normal'/>
                        <Button type='submit' color='primary' variant='contained'>Create</Button>
                    </form>
                    <List>
                        {exercises.map(({id, title}) => <ListItem key={id}>
                            <ListItemText primary={title}/>
                            <ListItemSecondaryAction>
                                <IconButton color='primary' onClick={() => this.handleDelete(id)}><Delete/></IconButton>
                            </ListItemSecondaryAction></ListItem>
                        )}
                    </List>
                </Paper>
            </Container>
        }
    }
);